import { useState, useEffect } from 'react';

import Fade from '@material-ui/core/Fade'

import NoobFilter from "../../components/NoobFilter"
import NoobTable from "../../components/NoobTable"
import { transcriptions } from '../../asset/transcriptions'

const Transcriptions = (props) => {
  const { setTitle, setFooterAppear } = props
  const [rows, setRows] = useState([])

  useEffect(() => {
    setTitle("Transcription")
    setRows(transcriptions)
  }, [setTitle, setRows])

  return (
    <Fade in={true} timeout={1000} onEntered={() => setFooterAppear(true)}>
      <div>
        <NoobFilter rows={rows} setRows={setRows} data={transcriptions} />
        <br />
        <NoobTable rows={rows} rowsPerPage={10} link={"#/transcriptions"} />
      </div>
    </Fade>
  )
}

export default Transcriptions