import React from 'react';

import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineDot from '@material-ui/lab/TimelineDot';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import PeopleIcon from '@material-ui/icons/People';
import MicIcon from '@material-ui/icons/Mic';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import DescriptionIcon from '@material-ui/icons/Description';
import AccountBalanceIcon from '@material-ui/icons/AccountBalance';
import WorkIcon from '@material-ui/icons/Work';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import { Tooltip } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  timeline: {
    transform: "rotate(90deg) translateY(400px)",
    margin: "-25rem",
  },
  timelineIcon: {
    display: "inline-block",
    transform: "rotate(-90deg)"
  },
  timelineTopStamp: {
    display: "inline-block",
    transform: "rotate(-90deg) translateX(-0.3rem) translateY(1.4rem)",
  },
  timelineBottomStamp: {
    display: "inline-block",
    transform: "rotate(-90deg) translateX(-0.3rem) translateY(-1.4rem)",
  },
  timelineTodayStamp: {
    display: "inline-block",
    transform: "rotate(-90deg) translateX(-0.3rem) translateY(-0.5rem)",
  },
  paper: {
    padding: '6px 16px',
  },
}));

const Item = (props) => {
  const { information, date, top, iconType } = props
  const classes = useStyles()
  const stampClass = top ? classes.timelineTopStamp : classes.timelineBottomStamp
  return (
    <TimelineItem>
      <TimelineSeparator>
        <Tooltip title={
          <React.Fragment>
            <Typography component="h1" variant="subtitle1">
              {information}
            </Typography>
          </React.Fragment>
        }>
          {renderTimeLineIcon(iconType, classes.timelineIcon)}
        </Tooltip>
        <TimelineConnector />
      </TimelineSeparator>
      <TimelineContent>
        {renderStamp(date, stampClass)}
      </TimelineContent>
    </TimelineItem>
  )
}

const renderTimeLineIcon = (iconType, timelineIcon) => {
  switch (iconType) {
    case "people": return (<TimelineDot color="secondary" variant="outlined">
      <PeopleIcon className={timelineIcon} />
    </TimelineDot>)
    case "mic": return (
      <TimelineDot color="secondary" variant="outlined">
        <MicIcon className={timelineIcon} />
      </TimelineDot>
    )
    case "work": return (
      <TimelineDot color="secondary" variant="outlined">
        <WorkIcon className={timelineIcon} />
      </TimelineDot>
    )
    case "calendar": return (
      <TimelineDot color="secondary" variant="outlined">
        <CalendarTodayIcon className={timelineIcon} />
      </TimelineDot>
    )
    case "description": return (
      <TimelineDot color="secondary" variant="outlined">
        <DescriptionIcon className={timelineIcon} />
      </TimelineDot>
    )
    case "account": return (
      <TimelineDot color="secondary" variant="outlined">
        <AccountBalanceIcon className={timelineIcon} />
      </TimelineDot>
    )
    default: return (
      <TimelineDot color="secondary"/>
    )
  }
}

const renderStamp = (date, className) => {
  return (
    <Typography variant="subtitle1" component="h6" className={className}>
      {date}
    </Typography>
  )
}


const Journey = (props) => {
  const classes = useStyles()
  return (
    <Timeline className={classes.timeline} align="alternate">
      <Item information="Start an Open Source Project" date="Dec 2022" top={false} />
      <Item information="Converse in Japanese" date="Dec 2021" top={true} />
      <TimelineItem>
        <TimelineSeparator>
          <Tooltip title={
            <React.Fragment>
              <Typography component="h1" variant="subtitle1">
                Final Year of University
                </Typography>
              <Typography component="h1" variant="subtitle1">
                Intern @ Learnseeker
              </Typography>
            </React.Fragment>
          }>
            <TimelineDot color="secondary" variant="outlined">
              <LocationOnIcon className={classes.timelineIcon} />
            </TimelineDot>
          </Tooltip>
          <TimelineConnector />
        </TimelineSeparator>
        <TimelineContent>
          <Typography variant="subtitle1" component="h6" className={classes.timelineTodayStamp}>
            Today
          </Typography>
        </TimelineContent>
      </TimelineItem>
      <Item information="Joined SIM IONS Exco as Technical Lead" date="July 2020" top={true} iconType="people" />
      <Item information="Project Mesh Final Pitch" date="June 2020" top={false} iconType="mic" />
      <Item information="Joined Learnseeker as Software Engineering Intern" date="Apr 2020" top={true} iconType="work" />
      <Item information="Organised Phoenix Week" date="Jan 2020" top={false} iconType="calendar" />
      <Item information="Joined SIM IONS as Ambassadors & Subcommittee" date="Oct 2019&nbsp;" top={true} iconType="people" />
      <Item information="Awarded with University of London Academic Achievement" date="Sept 2019" top={false} iconType="description" />
      <Item information="Passed JLPT N1" date="Aug 2019" top={true} iconType="description" />
      <Item information="Enrolled into University of London Bachelor of Science in Computing and Information System" date="Sept 2018" top={false} iconType="account" />
      <Item information="Passed ABRSM Piano Grade 5" date="Apr 2018" top={true} iconType="description" />
      <Item information="Finished National Service" date="Feb 2018" top={false} iconType="work" />
      <Item information="Passed ABRSM Piano Grade 5" date="Apr 2018" top={true} iconType="description" />
    </Timeline>
  )
}

export default Journey