import React, { useState } from 'react'
import {
  makeStyles,
  Typography,
  IconButton,
  Grid,
  Collapse,
  TextField,
} from '@material-ui/core'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';


const useStyles = makeStyles((theme) => ({
  header: {
    display: 'flex',
  },
  addButton: {
    alignSelf: 'flex-start',

    "&:hover": {
      background: theme.palette.primary.main
    },
  },

  filterArea: {
    alignSelf: 'center',
    marginTop: theme.spacing(2)
  },


}));
const NoobFilter = (props) => {
  const classes = useStyles()
  const { setRows, data } = props
  const [filterExpand, setFilterExpand] = useState(false)

  const handleExpand = () => {
    setFilterExpand(!!!filterExpand)
  }
  const handleTitleFilter = (event) => {
    if (!!!event.target.value) {
      setRows(data)
    }
    setRows(data.filter(row => {
      return row.title.toLowerCase().includes(event.target.value.toLowerCase())
    }))
  }

  return (
    <React.Fragment>
      <Collapse in={filterExpand} collapsedHeight={30} className={classes.filterArea}>
        <Grid container direction="row" alignItems="flex-start" spacing={0}>
          <Grid item>
            <Typography>Filter</Typography>
          </Grid>
          <Grid item>
            <IconButton size="small" style={{ marginLeft: "-10%", marginTop: "-10%" }} onClick={handleExpand}>
              <ExpandMoreIcon />
            </IconButton>
          </Grid>
          <Grid item xs={10} />
          <Grid item xs={3} >
            <TextField name="title" label="Title" onChange={handleTitleFilter} />
          </Grid>
        </Grid>
      </Collapse>
    </React.Fragment>
  )
}

export default NoobFilter