import { useState, useEffect } from 'react';

import Fade from '@material-ui/core/Fade'

import NoobFilter from "../../components/NoobFilter"
import NoobTable from "../../components/NoobTable"
import { projects } from '../../asset/projects'
import { PROJECTS } from '../../constants/titles';
import { LINK_PROJECTS } from '../../constants/links';

const Projects = (props) => {
  const { setTitle, setFooterAppear } = props
  const [rows, setRows] = useState([])

  useEffect(() => {
    setTitle(PROJECTS)
    setRows(projects)
  }, [setTitle, setRows])

  return (
    <Fade in={true} timeout={1000} onEntered={() => setFooterAppear(true)}>
      <div>
        <NoobFilter rows={rows} setRows={setRows} data={projects}/>
        <br />
        <NoobTable rows={rows} rowsPerPage={10} link={LINK_PROJECTS} />
      </div>
    </Fade>
  )
}

export default Projects