import React, { useState, useEffect, useRef } from 'react'
import CssBaseline from '@material-ui/core/CssBaseline'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import useScrollTrigger from '@material-ui/core/useScrollTrigger'
import Slide from '@material-ui/core/Slide'
import IconButton from '@material-ui/core/IconButton'
import Paper from '@material-ui/core/Paper'
import Button from '@material-ui/core/Button'
import Fade from '@material-ui/core/Fade'
import DescriptionIcon from '@material-ui/icons/Description';
import MenuList from '@material-ui/core/MenuList'
import MenuItem from '@material-ui/core/MenuItem'
import Popper from '@material-ui/core/Popper'
import Grow from '@material-ui/core/Grow'
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import MenuIcon from '@material-ui/icons/Menu';

import { ThemeProvider, createMuiTheme, makeStyles } from "@material-ui/core";

import Joyride, { STATUS } from 'react-joyride';
import { HashRouter, Route, Switch, Redirect } from 'react-router-dom'

import { light, dark } from './constants/themes'
import Home from './scenes/Home'
import Blogs from './scenes/Blogs'
import Blog from './scenes/Blog'
import Transcriptions from './scenes/Transcriptions'
import Transcription from './scenes/Transcription'
import {
  ROUTE_ALL,
  ROUTE_BLOG,
  ROUTE_BLOGS,
  ROUTE_HOME,
  ROUTE_PROJECT,
  ROUTE_PROJECTS,
  ROUTE_TRANSCRIPTION,
  ROUTE_TRANSCRIPTIONS
} from './constants/routes'

import {
  LINK_RESUME,
  LINK_PROJECTS,
  LINK_TRANSCRIPTIONS,
  LINK_LINKEDIN,
  LINK_LINKEDIN_LOGO,
  LINK_YOUTUBE,
  LINK_YOUTUBE_LOGO
} from './constants/links'
import {
  HOME,
  BLOGS,
  PROJECTS,
  TRANSCRIPTIONS,
  CONTACT
} from './constants/titles'
import Projects from './scenes/Projects'
import Project from './scenes/Project'

const useStyles = makeStyles(theme => ({
  app_bar: {
    padding: 0,
    display: 'flex'
  },
  footer: {
    marginTop: theme.spacing(1),
    borderRadius: 0,
    backgroundColor: 'rgb(185 185 185 / 38%)',
    padding: theme.spacing(2),
    display: 'flex',
    flexDirection: 'column'
  },
  title: {
    flexGrow: 1
  },
  selected: {
    textDecoration: 'solid underline #E9D296',
    textUnderlinePosition: 'under'
  },
  page_content: {
    padding: theme.spacing(2)
  },
  youtube_image: {
    margin: theme.spacing(1),
    marginTop: theme.spacing(1.25)
  },
  footer_content: {
    display: 'flex',
    flexDirection: 'row'
  },
  resume: {
    width: theme.spacing(5),
    height: theme.spacing(5),
    marginRight: theme.spacing(1)
  }
}))

const HideOnScroll = (props) => {
  const { children, window } = props;
  const trigger = useScrollTrigger({ target: window ? window() : undefined });
  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

const App = (props) => {
  const classes = useStyles()
  const storageTheme = localStorage.getItem("theme")
  const [theme, setTheme] = useState(storageTheme === "light" || storageTheme === null);
  const [title, setTitle] = useState("")
  const [run, setRun] = useState(!!!localStorage.getItem("experiencedUser"))
  const [footerAppear, setFooterAppear] = useState(false)
  const [menuOpen, setMenuOpen] = useState(false);
  const anchorRef = useRef(null);
  const appliedTheme = createMuiTheme(theme ? light : dark);
  const steps = [
    {
      content: (<h4>Click here for dark mode</h4>),
      placement: 'bottom',
      target: '.icon',
      spotlightPadding: 0,
      disableBeacon: true,
    }
  ]

  const handleToggle = () => {
    setMenuOpen((prevOpen) => !prevOpen);
  };

  const handleClose = (event) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }
    setMenuOpen(false);
  };

  const handleMenuClick = (event, link) => {
    if (anchorRef.current && anchorRef.current.contains(event.target)) {
      return;
    }
    window.location.href = link
    setMenuOpen(false);
  }

  const handleListKeyDown = (event) => {
    if (event.key === 'Tab') {
      event.preventDefault();
      setMenuOpen(false);
    }
  }

  // return focus to the button when we transitioned from !open -> open
  const prevOpen = useRef(menuOpen);
  useEffect(() => {
    if (prevOpen.current === true && menuOpen === false) {
      anchorRef.current.focus();
    }

    prevOpen.current = menuOpen;
  }, [menuOpen]);

  const handleThemeChange = (event) => {
    if (theme) {
      localStorage.setItem("theme", "dark")
    } else {
      localStorage.setItem("theme", "light")
    }
    setTheme(!theme)
  }

  const handleJoyrideCallback = (data) => {
    const { status } = data
    const finishedStatuses = [STATUS.FINISHED, STATUS.SKIPPED]

    if (finishedStatuses.includes(status)) {
      setRun(false)
      localStorage.setItem("experiencedUser", true)
    }
  };

  useEffect(() => {
    setFooterAppear(false)
  }, [title])

  return (
    <ThemeProvider theme={appliedTheme}>
      <CssBaseline />
      <Joyride
        callback={handleJoyrideCallback}
        run={run}
        scrollToFirstStep={true}
        steps={steps}
        hideBackButton={true}
        styles={{
          options: {
            primaryColor: appliedTheme.palette.primary.main,
            width: 300,
            zIndex: 10000,
          },
        }}
      />
      <HashRouter >
        <HideOnScroll {...props}>
          <AppBar >
            <Toolbar className={classes.app_bar} variant="dense">
              <IconButton className="icon" onClick={handleThemeChange}>
                <img src={theme ? "./logo_inverse.png" : "./logo.png"} alt="noobymage9 Logo" />
              </IconButton>
              <Typography className={classes.title} color="textPrimary" variant="h6">{title}</Typography>
              <Button href="#/home" color="inherit" size="large" className={title === "Home" ? classes.selected : ""} >{HOME}</Button>
              <Button href="#/blogs" color="inherit" size="large" className={title === "Blog" ? classes.selected : ""} >{BLOGS}</Button>
              <IconButton
                ref={anchorRef}
                aria-controls={menuOpen ? 'menu-list-grow' : undefined}
                aria-haspopup="true"
                onClick={handleToggle}
              >
                <MenuIcon />
              </IconButton>
              <Popper open={menuOpen} anchorEl={anchorRef.current} role={undefined} transition disablePortal>
                {({ TransitionProps, placement }) => (
                  <Grow
                    {...TransitionProps}
                    style={{ transformOrigin: placement === 'bottom' ? 'center top' : 'center bottom' }}
                  >
                    <Paper>
                      <ClickAwayListener onClickAway={handleClose}>
                        <MenuList autoFocusItem={menuOpen} id="menu-list-grow" onKeyDown={handleListKeyDown}>
                          <MenuItem onClick={event => handleMenuClick(event, LINK_PROJECTS)}>{PROJECTS}</MenuItem>
                          <MenuItem onClick={event => handleMenuClick(event, LINK_TRANSCRIPTIONS)}>{TRANSCRIPTIONS}</MenuItem>
                        </MenuList>
                      </ClickAwayListener>
                    </Paper>
                  </Grow>
                )}
              </Popper>
            </Toolbar>
          </AppBar>
        </HideOnScroll>

        <Toolbar />

        <div className={classes.page_content}>
          <Switch>
            <Route exact path={ROUTE_HOME}>
              <Home setTitle={setTitle} setFooterAppear={setFooterAppear} />
            </Route>
            <Route exact path={ROUTE_BLOGS}>
              <Blogs setTitle={setTitle} setFooterAppear={setFooterAppear} />
            </Route>
            <Route path={ROUTE_BLOG}>
              <Blog setTitle={setTitle} setFooterAppear={setFooterAppear} />
            </Route>
            <Route exact path={ROUTE_TRANSCRIPTIONS}>
              <Transcriptions setTitle={setTitle} setFooterAppear={setFooterAppear} />
            </Route>
            <Route path={ROUTE_TRANSCRIPTION}>
              <Transcription setTitle={setTitle} setFooterAppear={setFooterAppear} />
            </Route>
            <Route exact path={ROUTE_PROJECTS}>
              <Projects setTitle={setTitle} setFooterAppear={setFooterAppear} />
            </Route>
            <Route path={ROUTE_PROJECT}>
              <Project setTitle={setTitle} setFooterAppear={setFooterAppear} />
            </Route>
            <Route path={ROUTE_ALL}>
              <Redirect to={ROUTE_HOME} />
            </Route>
          </Switch>
        </div>
      </HashRouter>

      <Fade in={footerAppear} timeout={{ enter: 1000, exit: 0 }}>
        <footer>
          <Paper className={classes.footer} elevation={0}>
            <Typography color="textPrimary" variant="h6" component="h6">{CONTACT}</Typography>
            <div className={classes.footer_content}>
              <Typography color="textPrimary" variant="subtitle1" component="h6">
                <a href={LINK_RESUME} target="_blank" rel="noreferrer">
                  <DescriptionIcon className={classes.resume} />
                </a>
              </Typography>
              <Typography color="textPrimary" variant="subtitle1" component="h6">
                <a href={LINK_LINKEDIN} target="_blank" rel="noreferrer">
                  <img src={LINK_LINKEDIN_LOGO} width="96rem" height="48rem" alt="LinkedIn" />
                </a>
              </Typography>
              <Typography color="textPrimary" variant="subtitle1" component="h6">
                <a href={LINK_YOUTUBE} target="_blank" rel="noreferrer">
                  <img className={classes.youtube_image} src={LINK_YOUTUBE_LOGO} width="96rem" height="28rem" alt="Youtube" />
                </a>
              </Typography>
            </div>
          </Paper>
        </footer>
      </Fade>
    </ThemeProvider>
  );
}

export default App;
