import React, { useEffect } from 'react'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Slide from '@material-ui/core/Slide'
import Divider from '@material-ui/core/Divider'
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core'
import { useParams } from 'react-router-dom'

import { projects } from '../../asset/projects'

const useStyles = makeStyles(theme => ({
  card: {
    borderRadius: theme.spacing(3),
    marginTop: theme.spacing(2),
    padding: theme.spacing(2),
    maxWidth: theme.spacing(96),
    margin: 'auto'
  },
  card_content: {
    display: 'flex',
    flexDirection: 'column'
  },
  showcase: {
    borderRadius: theme.spacing(3),
    minWidth: theme.spacing(80),
    outline: 'none',
  },
}))

const Project = (props) => {
  const { id } = useParams()
  const classes = useStyles()
  const { setTitle, setFooterAppear } = props
  const project = projects.filter(project => project.id === parseInt(id))[0]

  useEffect(() => {
    setTitle(project.title)
  }, [setTitle, id, project])

  return (
    <React.Fragment>
      <Slide in={true} direction="up" timeout={1000} onEntered={() => setFooterAppear(true)}>
        <Card className={classes.card}>
          <CardContent className={classes.card_content}>
            <Typography align="center" variant="h4" component="h4">{project.title}</Typography>
            <Divider />

            {project.contents.map((content, index) => (
              <React.Fragment key={index}>
                <br />
                <Typography variant="subtitle1" component="h6">{content}</Typography>
              </React.Fragment>
            ))}
            <br />

            <Typography variant="subtitle1" component="h6">Languages: {project.languages.join(', ')}</Typography>
            <br />

            <Typography variant="subtitle1" component="h6">Libraries: {project.libraries.join(', ')}</Typography>
            <br />

            <Typography variant="subtitle1" component="h6">Libraries: {project.tools.join(', ')}</Typography>
            <br />

            {
              project.showcase ?
                <video className={classes.showcase} controls Focus>
                  <source src={project.showcase} type="video/mp4" />
                </video>
                : false
            }

          </CardContent>
        </Card>
      </Slide>
    </React.Fragment>
  )
}

export default Project