import React, { useEffect } from 'react'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Slide from '@material-ui/core/Slide'
import Divider from '@material-ui/core/Divider'
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core'
import { useParams } from 'react-router-dom'

import { blogs } from '../../asset/blogs'

const useStyles = makeStyles(theme => ({
  card: {
    borderRadius: theme.spacing(3),
    marginTop: theme.spacing(2),
    padding: theme.spacing(2),
    maxWidth: theme.spacing(96),
    margin: 'auto'
  },
  card_content: {
    display: 'flex',
    flexDirection: 'column'
  },
}))

const Blog = (props) => {
  const { id } = useParams()
  const classes = useStyles()
  const { setTitle, setFooterAppear } = props
  const blog = blogs.filter(blog => blog.id === parseInt(id))[0]
  
  useEffect(() => {
    setTitle(`Blog ${blog.id + 1} on ${blog.date}`)
  }, [setTitle, id, blog])

  return (
    <React.Fragment>
      <Slide in={true} direction="up" timeout={1000} onEntered={() => setFooterAppear(true)}>
        <Card className={classes.card}>
          <CardContent className={classes.card_content}>
            <Typography align="center" variant="h4" component="h4">{blog.title}</Typography>
            <Divider />
            {blog.contents.map((content, index) => (
              <React.Fragment key={index}>
                <br />
                <Typography variant="subtitle1" component="h6">{content}</Typography>
              </React.Fragment>
            ))}
          </CardContent>
        </Card>
      </Slide>
    </React.Fragment>
  )
}

export default Blog