import React, { useEffect } from 'react'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Slide from '@material-ui/core/Slide'
import Divider from '@material-ui/core/Divider'
import Typography from '@material-ui/core/Typography'
import { Button, CardActions, makeStyles } from '@material-ui/core'
import { useParams } from 'react-router-dom'

import { transcriptions } from '../../asset/transcriptions'

const useStyles = makeStyles(theme => ({
  card: {
    borderRadius: theme.spacing(3),
    marginTop: theme.spacing(2),
    padding: theme.spacing(2),
    maxWidth: theme.spacing(96),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    margin: 'auto'
  },
  card_content: {
    display: 'flex',
    flexDirection: 'column'
  },
  youtube: {
    margin: theme.spacing(3),
    borderRadius: theme.spacing(3),
    minWidth: theme.spacing(80),
    minHeight: theme.spacing(60)
  }
}))

const Transcription = (props) => {
  const { id } = useParams()
  const classes = useStyles()
  const { setTitle, setFooterAppear } = props
  const transcription = transcriptions.filter(transcription => transcription.id === parseInt(id))[0]

  useEffect(() => {
    setTitle(transcription.title)
  }, [setTitle, transcription])

  return (
    <React.Fragment>
      <Slide in={true} direction="up" timeout={1000} onEntered={() => setFooterAppear(true)}>
        <Card className={classes.card}>
          <CardContent className={classes.card_content}>
            <Typography align="center" variant="h4" component="h4">{transcription.title}</Typography>
            <Divider />
              <iframe title={transcriptions.title} class={classes.youtube} src={transcription.link}></iframe>
          </CardContent>
          <CardActions>
            <Button href={transcription.file} target="_blank" variant="outlined" color="primary">
              Score Sheet
            </Button>
          </CardActions>
        </Card>
      </Slide>
    </React.Fragment>
  )
}

export default Transcription